import { Component, Injector } from '@angular/core';
import { env } from '../../env';
import { EditComponentBase } from '../../../util';
import { AccountViewModel } from './model/account-view-model';

/**
 * 账户编辑页
 */
@Component({
    selector: 'account-edit',
    templateUrl: !env.dev() ? './html/account-edit.component.html' : '/view/finances/account/edit'
})
export class AccountEditComponent extends EditComponentBase<AccountViewModel> {
    /**
     * 初始化账户编辑页
     * @param injector 注入器
     */
    constructor(injector: Injector) {
        super(injector);
    }

    /**
     * 获取基地址
     */
    protected getBaseUrl() {
        return "account";
    }
}