import { Component, Injector } from '@angular/core';
import { env } from '../../env';
import { EditComponentBase } from '../../../util';
import { AccountDetailViewModel } from './model/accountDetail-view-model';

/**
 * 账户明细详情页
 */
@Component({
    selector: 'accountDetail-detail',
    templateUrl: !env.dev() ? './html/accountDetail-detail.component.html' : '/view/finances/accountDetail/detail'
})
export class AccountDetailDetailComponent extends EditComponentBase<AccountDetailViewModel> {
    /**
     * 初始化账户明细详情页
     * @param injector 注入器
     */
    constructor(injector: Injector) {
        super(injector);
    }

    /**
     * 获取基地址
     */
    protected getBaseUrl() {
        return "accountDetail";
    }
}