import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AccountListComponent } from './account/account-list.component';
import { AccountEditComponent } from './account/account-edit.component';
import { AccountDetailComponent } from './account/account-detail.component';
import { AccountDetailListComponent } from './accountDetail/accountDetail-list.component';
import { AccountDetailEditComponent } from './accountDetail/accountDetail-edit.component';
import { AccountDetailDetailComponent } from './accountDetail/accountDetail-detail.component';

//路由配置
const routes: Routes = [
    {
        path: '',
        children: [
            {path: 'account', children: [
                { path: '', component: AccountListComponent },
                { path: 'create', component: AccountEditComponent },
                { path: 'edit/:id', component: AccountEditComponent },
                { path: 'detail/:id', component: AccountDetailComponent }
            ]},
            {path: 'accountDetail', children: [
                { path: '', component: AccountDetailListComponent },
                { path: 'create', component: AccountDetailEditComponent },
                { path: 'edit/:id', component: AccountDetailEditComponent },
                { path: 'detail/:id', component: AccountDetailDetailComponent }
            ]},
        ]
    }
];

/**
 * finances路由模块
 */
@NgModule({
    imports: [RouterModule.forChild(routes)]
})
export class FinanceRoutingModule { }